<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我参与的活动</div>
    <common-table
      :columns="tableColumns"
      :datas="tableData"
      :pagger="false"
      th-class="table-innerhead"
      border
    >
      <template slot="oprate" slot-scope="scope">
        <el-button type="primary" size="mini" @click="look(scope.row)">查看</el-button>
      </template>
    </common-table>
    <bottom-pager :total="total" @change="pageChange"/>
  </div>
</template>
<script>
import commonTable from "../../../components/commonTable.vue";
import bottomPager from "../../../components/personal/bottomPager.vue";
export default {
  components: {
    commonTable,
    bottomPager
  },
  data() {
    return {
      tableColumns: [
        {
          prop: "name",
          label: "活动名称"
        },
        {
          prop: "subject",
          label: "所属科目"
        },
        {
          prop: "date",
          label: "发布时间"
        },
        {
          prop: "owner",
          label: "发布人"
        },
        {
          prop: "type",
          label: "活动类型"
        },
        {
          prop: "number",
          label: "参与人数"
        },
        {
          prop: "oprate",
          label: "操作",
          slot: true,
          width: 100
        }
      ],
      tableData: [
        {
          id: "123",
          name: "牛津英语词典解析",
          subject: "大学英语",
          date: "2019-05-12 14:00",
          owner: "江丽颜",
          type: "头脑风暴",
          number: "53"
        }
      ],
      total: 1
    };
  },
  methods: {
    look(row) {
      this.$router.push(`/main/studentSpace/activities/detail/${row.id}`);
    }
  }
};
</script>
<style lang="less" scoped>
</style>



